<script>
	import _ from 'underscore'
	import axios from 'axios'
	import moment from 'moment'
	import { videoPlayer } from 'vue-video-player'

	export default {
		name: "CensusTrackManage",
		data() {
			return {
				query: {
					sbmc: '',
					gzzmc: '',
					stime:'',
					etime:''
				},
				dialog: {
					display: false,
					title: '详情信息列表',
					activeNames: ['属性'],
					attributesTitle: '',
					data: []
				},
				dialogImage: {
					display: false,
					title: '图片',
					mediaList: [],
					mediaIndex: ''
				},
				gjpcmap: null,
				managelist: null,
				workGroupList: [],
				workGroupLine: []
			}
		},
		mounted() {
			this.$nextTick(() => {
				this.gjpcmap = new AMap.Map('gjpcmap', {
					resizeEnable: true, //是否监控地图容器尺寸变化
					mapStyle: "amap://styles/fresh"
				});
				this.query.stime = this.fomarttime(new Date(new Date().getTime() - 1000*60*60*2))
				this.query.etime = this.fomarttime(new Date())
				this.getfourworks();
				this.getWorkGroupList();
			});
		},
		computed: {
			attributesWWType() {
				return this.dialog.attributesTitle === '工井'
			},
			attributesDSType() {
				return this.dialog.attributesTitle === '配电站'
			},
			attributesTOType() {
				return this.dialog.attributesTitle === '杆塔'
			},
			attributesODType() {
				return this.dialog.attributesTitle === '其他'
			},
			dialogInfo() {
				const data = this.dialog.data;
				if (data.info && data.info.length > 0) return data.info[0];
				return {};
			},
			dialogCableList() {
				const data = this.dialog.data;
				if (data.dl && data.dl.length > 0) return data.dl;
				return [];
			},
			imageOneList() {
				const data = this.dialog.data;
				if (data.one && data.one.length > 0) return data.one;
				return {};
			},
			imageTwoList() {
				const data = this.dialog.data;
				if (data.two && data.two.length > 0) return data.two;
				return {};
			},
			imageThreeList() {
				const data = this.dialog.data;
				if (data.three && data.three.length > 0) return data.three;
				return {};
			},
			imageFourList() {
				const data = this.dialog.data;
				if (data.four && data.four.length > 0) return data.four;
				return {};
			},
			imageFiveList() {
				const data = this.dialog.data;
				if (data.five && data.five.length > 0) return data.five;
				return {};
			},
			mediaUrl() {
				const {mediaList, mediaIndex} = this.dialogImage;
				return mediaList[mediaIndex];
			}
		},
		methods: {
			fomarttime(date){
				return moment(date).format('YYYY-MM-DD HH:mm:ss')
			},
			getCensusTrackList(id, table) {
				const baseURL = this.baseURL;
				axios.get('company/getpcgj', {
					baseURL,
					params: {id, table}
				}).then(res => {
					this.dialog.data = res.data;
				})
			},
			getWorkGroupList() {
				const baseURL = this.baseURL
				axios.get('company/getgzz', {
					baseURL
				}).then(res => {
					this.workGroupList = res.data;
				})
			},
			handleWWListOfDetailsClick(id) {
				this.dialog.display = true;
				this.dialog.attributesTitle = '工井';
				this.getCensusTrackList(id, 2);
			},
			handleDSListOfDetailsClick(id) {
				this.dialog.display = true;
				this.dialog.attributesTitle = '配电站';
				this.getCensusTrackList(id, 3);
			},
			handleTOListOfDetailsClick(id) {
				this.dialog.display = true;
				this.dialog.attributesTitle = '杆塔';
				this.getCensusTrackList(id, 1);
			},
			handleODListOfDetailsClick(id) {
				this.dialog.display = true;
				this.dialog.attributesTitle = '其他';
				this.getCensusTrackList(id, 4);
			},
			handleCollapseChange() {},
			handleDialogClose() {
				this.dialog.display = false;
				switch (this.dialog.attributesTitle) {
					case '工井':
						break;
					case '配电站':
						break;
					case '杆塔':
						break;
					case '其他':
						break;
				}
				this.dialog.attributesTitle = '';
			},
			getfourworks() {
				const baseURL = this.baseURL
				axios.get('company/getfourworks', {
					baseURL,
					params: {
						'stime':'',
						'etime':'',
						'gzzmc':''
					}
				}).then(res => {
					this.managelist = res.data;
					console.log(res.data)
					let gt = res.data.GT;
					let gzj = res.data.GZJ;
					let pdz = res.data.PDZ;
					let qt = res.data.QT;
					this.gjpcmap.clearMap();
					for(let i=0; i<gt.length; i++){
						this.addmar(gt[i],0);
					}
					for(let i=0; i<gzj.length; i++){
						this.addmar(gzj[i],1);
					}
					for(let i=0; i<pdz.length; i++){
						this.addmar(pdz[i],2);
					}
					for(let i=0; i<qt.length; i++){
						this.addmar(qt[i],3);
					}
				})
			},
			handleQueryGroupNameTime() {
				const baseURL = this.baseURL
				const {stime,etime,gzzmc} = this.query;
				if(stime === ''){
					this.$message({
						message: '开始时间不能为空！',
						type: 'warning'
					});
					return;
				}
				if(etime === ''){
					this.$message({
						message: '结束时间不能为空！',
						type: 'warning'
					});
					return;
				}
				if(gzzmc === ''){
					this.$message({
						message: '工作组名称不能为空！',
						type: 'warning'
					});
					return;
				}
				axios.get('company/getfourworks', {
					baseURL,
					params: {
					stime,etime,gzzmc
				}
				}).then(res => {
					this.managelist = res.data;
					let gt = res.data.GT;
					let gzj = res.data.GZJ;
					let pdz = res.data.PDZ;
					let qt = res.data.QT;
					if(gt.length == 0 &&gzj == 0 &&pdz.length == 0 &&qt.length == 0){
						this.$message({
							message: '该工作组在该时间段没有记录！',
							type: 'warning'
						});
						return;
					}
					let Line = [];
					this.gjpcmap.clearMap();
					for(let i=0; i<gt.length; i++){
						this.addmar(gt[i],0);
						Line.push({'Longitude':gt[i].Longitude,'Latitude':gt[i].Latitude,'WorksTime':gt[i].WorksTime})
					}
					for(let i=0; i<gzj.length; i++){
						this.addmar(gzj[i],1);
						Line.push({'Longitude':gzj[i].Longitude,'Latitude':gzj[i].Latitude,'WorksTime':gzj[i].WorksTime})
					}
					for(let i=0; i<pdz.length; i++){
						this.addmar(pdz[i],2);
						Line.push({'Longitude':pdz[i].Longitude,'Latitude':pdz[i].Latitude,'WorksTime':pdz[i].WorksTime})
					}
					for(let i=0; i<qt.length; i++){
						this.addmar(qt[i],3);
						Line.push({'Longitude':qt[i].Longitude,'Latitude':qt[i].Latitude,'WorksTime':qt[i].WorksTime})
					}
					this.workGroupLine = [];
					for(let i=0; i<Line.length; i++){
						this.workGroupLine[i] = [Line[i].Longitude,Line[i].Latitude]
					}
					this.addLine()
				})
			},
			addLine: function(){
				let polyline = new AMap.Polyline({
					path: this.workGroupLine,
					isOutline: true,
					outlineColor: '#ffeeff',
					borderWeight: 3,
					strokeColor: "#3366FF",
					strokeOpacity: 1,
					strokeWeight: 6,
					// 折线样式还支持 'dashed'
					strokeStyle: "solid",
					// strokeStyle是dashed时有效
					strokeDasharray: [10, 5],
					lineJoin: 'round',
					lineCap: 'round',
					zIndex: 50,
				})
				polyline.setMap(this.gjpcmap)
			},
			addmar: function (obj, type) {
				let icon = '';
				var details = $('<div class="tw-map-details">');
				if (type === 0) {
					icon = '/ccensus//img/gt.png';
					details.append($('<p class="tw-map-details-item">').text(`设备类型: ${obj.dtype = '杆塔'}`));
					details.append($('<p class="tw-map-details-item">').text(`设备名称：${obj.GT_name}`));
				} else if (type === 1) {
					if (obj.GZJ_lidtype === '1') icon = '/ccensus//img/gzjf.png';
					else if (obj.GZJ_lidtype === '2') icon = '/ccensus//img/gzjy.png';
					else if (obj.GZJ_lidtype === '3') icon = '/ccensus//img/gzjh.png';
					details.append($('<p class="tw-map-details-item">').text(`设备类型: ${obj.dtype = '工作井'}`));
					details.append($('<p class="tw-map-details-item">').text(`设备名称：${obj.GZJ_name}`));
				} else if (type === 2) {
					icon = '/ccensus//img/pdz.png';
					details.append($('<p class="tw-map-details-item">').text(`设备类型: ${obj.dtype = '配电站'}`));
					details.append($('<p class="tw-map-details-item">').text(`设备名称：${obj.PDZ_name}`));
				} else if (type === 3) {
					obj.dtype = '其他';
					if (obj.TYPE === '1') {
						icon = '/ccensus//img/hwg.png';
						details.append($('<p class="tw-map-details-item">').text('设备类型: 环网柜'));
					} else if (obj.TYPE === '2') {
						icon = '/ccensus//img/byq.png';
						details.append($('<p class="tw-map-details-item">').text('设备类型: 变压器'));
					} else if (obj.TYPE === '3') {
						icon = '/ccensus//img/fzx.png';
						details.append($('<p class="tw-map-details-item">').text('设备类型: 分支箱'));
					} else if (obj.TYPE === '4') {
						icon = '/ccensus//img/qt.png';
						details.append($('<p class="tw-map-details-item">').text('设备类型: 其他'));
					}
					details.append($('<p class="tw-map-details-item">').text(`设备名称：${obj.QT_name}`));
				}
				details.append($('<p class="tw-map-details-item">').text('RFID编号：' + obj.RFID));
				details.append($('<p class="tw-map-details-item">').text('检查时间：' + moment(obj.WorksTime).format('YYYY-MM-DD HH:mm:ss')));
				details.append($('<p class="tw-map-details-item">').text('工作组名：' + obj.GZZ_name));
				details.append($('<p class="tw-map-details-item">').text('公司名称：' + obj.company));
				details.append($('<p class="tw-map-details-item">').text('联系电话：' + obj.phone));
				var button = $('<button type="button">').addClass('btn-queryDetailsInfo el-button el-button--primary el-button--small').text('查询详细信息').on('click', () => {this.handleQueryDetailsInfoClick(obj)});
				var details1 = $('<p class="tw-map-details-item">').css({height: '32px', lineHeight: '32px'}).append(button);
				details.append(details1);
				let marker1 = new AMap.Marker({
					map: this.gjpcmap,
					icon: icon,
					position: new AMap.LngLat(obj.Longitude, obj.Latitude),
					offset: new AMap.Pixel(-8, -8), //相对于基点的偏移位置
					draggable: false
				});
				marker1.vehicle = obj;
				let inforWindow = new AMap.InfoWindow({
					offset: new AMap.Pixel(0, 0),
					content: details[0]
				});
				AMap.event.addListener(marker1, "click", e => {
					inforWindow.open(this.gjpcmap, new AMap.LngLat(obj.Longitude, obj.Latitude));
				});
				AMap.event.addListener(this.gjpcmap, "click", e => {
					inforWindow.close();
				});
			},
			handleQueryDetailsInfoClick(item) {
				if (item.dtype === '杆塔') this.handleTOListOfDetailsClick(item.id);
				else if (item.dtype === '工作井') this.handleWWListOfDetailsClick(item.id);
				else if (item.dtype === '配电站') this.handleDSListOfDetailsClick(item.id);
				else if (item.dtype === '其他') this.handleODListOfDetailsClick(item.id);
			},
			handleDNameClick() {
				this.getfourworks();
				const {sbmc} = this.query;
				let mar = this.gjpcmap.getAllOverlays();
				let flat = false;
				for(let i=0; i<mar.length;i++){
					if(mar[i].hasOwnProperty('vehicle') && (mar[i].vehicle.PDZ_name === sbmc || mar[i].vehicle.GT_name === sbmc || mar[i].vehicle.GZJ_name === sbmc || mar[i].vehicle.QT_name === sbmc)){
						AMap.event.trigger(mar[i],'click');
						flat = true;
					}
				}
				if(!flat){
					this.$message({
						message: '无该设备名称',
						type: 'warning'
					});
				}
			},
			handleImageItemClick(imageList, index) {
				this.dialogImage.display = true;
				this.dialogImage.title = '图片';
				this.dialogImage.mediaList = imageList;
				this.dialogImage.mediaIndex = index;
			},
			handleVideoItemClick(videoList, index) {
				this.dialogImage.display = true;
				this.dialogImage.title = '视频';
				this.dialogImage.mediaList = videoList;
				this.dialogImage.mediaIndex = index;
			},
			handleDialogImageClose() {
				this.dialogImage.mediaList = [];
				this.dialogImage.mediaIndex = '';
			},
			handleDialogImageLeftClick() {
				this.dialogImage.mediaIndex = this.displayDirectionMedia(-1);
			},
			handleDialogImageRightClick() {
				this.dialogImage.mediaIndex = this.displayDirectionMedia(1);
			},
			displayDirectionMedia(number) {
				const {mediaList, mediaIndex} = this.dialogImage;
				return (mediaList.length + mediaIndex + number) % mediaList.length;
			},
			judgeMediaType(media) {
				let mediaUrl = media && media.Enclosure_Num || '';
				const fileName = mediaUrl.substring(mediaUrl.lastIndexOf('/') || 0);
				return fileName && fileName.indexOf('.mp4') >= 0;
			},
			getDialogImageUrl(item) {
				let url = item && item.Enclosure_Num || '';
				// return `${this.baseURL}company/getpuct?address=${url}`
				if(this.judgeMediaType(item)) return {
					width: '500',
					height: '300',
					language: 'zh-CN',
					playbackRates: [0.7, 1.0, 1.5, 2.0],
					sources: [{
						type: 'video/mp4',
						src: `${this.baseURL}company/getpuct?address=${url}`
					}]
				}
				else return `${this.baseURL}company/getpuct?address=${url}`
			},
			getImageURL(url) {
				return `${this.baseURL}company/getpuct?address=${url}`
			},
			getGJType(obj){
				if(obj === '1')
					return '二通';
				else if(obj === '2')
					return '三通'
				else if(obj === '3')
					return '四通'
				else if(obj === '4')
					return '异型'
			},
			getJGType(obj){
				if(obj === '1')
					return '方形';
				else if(obj === '2')
					return '圆形'
				else if(obj === '3')
					return '混合'
			}
		},
		components: {
			videoPlayer
		}
	}
</script>

<style lang="scss" scoped>
	.tw-map-query {
		&-item {
			margin-bottom: 10px;

			&:first-child {
				padding-bottom: 10px;
				border-bottom: 1px solid #eeeeee;
			}

			&:last-child {
				margin-bottom: 0;
			}
		}
		&-bar {
			background-color: #fff;
		}
	}

	.tw-list {
		position: relative;
		padding-bottom: 20px;
		border-top: 1px solid #eeeeee;
		border-bottom: 1px solid #eeeeee;

		&-item {
			position: relative;
			display: inline-block;
			width: 200px;

			&.dx2 {
				width: 200px;
			}

			&.db {
				width: 100%;
			}
		}

		&-title {
			position: absolute;
			top: 0;
			left: 0;
			display: inline-block;
			width: 6em;
			min-height: 30px;
			line-height: 35px;
			text-align: right;
			white-space: nowrap;
		}

		&-center {
			display: inline-block;
			width: calc(100% - 6em);
			margin-left: 6em;
			line-height: 35px;
			box-sizing: border-box;

			&.img-box {
				min-height: 50px;
				padding: 10px;
			}
		}
	}

	.tw-image {
		&-list {
			overflow-x: auto;
			min-height: 80px;
			line-height: 0;
			white-space: nowrap;
		}
		&-item{
			height: 60px;
			margin-right: 10px;
			&:last-child {
				margin-right: 0;
			}
		}
	}
	.tw-icon {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
	}

	.tw-button-item {
		position: absolute;
		left: 50%;
		bottom: -3px;
		padding: 2px 20px;
		border: 2px solid #409eff;
		border-top-left-radius: 50px;
		border-top-right-radius: 50px;
		font-size: 12px;
		line-height: 12px;
		cursor: pointer;
		transform: translateX(-50%) scale(.8);
	}

	.tw-dialog {
		&-image {
			width: 100%;
		}

		&-media {
			width: 100%;
			height: 300px;

			object, embed {
				width: 100%;
				height: 300px;
			}
		}

		&-left, &-right {
			position: absolute;
			top: 0;
			width: 100px;
			height: 100%;
			font-size: 60px;
			text-align: center;
			color: #fff;
			opacity: .6;
			user-select: none;
			cursor: pointer;

			&:hover {
				opacity: 1;
			}

			&:before {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
			}
		}

		&-left {
			left: 0;

			&:hover {
				background: linear-gradient(90deg, rgba(0, 0, 0, .4) 20%, transparent 100%);
			}
		}

		&-right {
			right: 0;

			&:hover {
				background: linear-gradient(90deg, transparent 20%, rgba(0, 0, 0, .4) 100%);
			}
		}
	}

	.imageHidePanel {
		/*display: none;*/

		&.active {
			display: block;
		}
	}

	.search-input {
		width: 160px;
	}
</style>